import React from "react"
import Gallery from 'react-photo-gallery'
import Lightbox from 'react-images'

import style from "./css/other.module.css"
import Layout from "../components/layout/layout"
import Navbar from "../components/navbar/navbar"
import Footer from "../components/footer/footer"

const contentful = require('contentful');
const { documentToHtmlString } = require('@contentful/rich-text-html-renderer');
const space_id = '0rndteXyTMOpyTLk0f0MsA';
const paths = require.context('../components/media/galleries/accessoriVino', false);
const photos = [];

const client = contentful.createClient({
  space: 'i9ffcls8piwd',
  accessToken: '0e901bffbe4847589adf6f981bf030c8d6071e5561d8a1ccd54114e98bc63191'
});

paths.keys().forEach((filename) => {
  const obj = {};
  obj.src = paths(filename);
  obj.width = 4;
  obj.height = 3;
  photos.push(obj);
});

class accessoriVino extends React.Component {
  constructor() {
    super();
    this.state = {
      currentImage: 0,
      data: null,
    };
    this.closeLightbox = this.closeLightbox.bind(this);
    this.openLightbox = this.openLightbox.bind(this);
    this.gotoNext = this.gotoNext.bind(this);
    this.gotoPrevious = this.gotoPrevious.bind(this);
  }
  componentDidMount() {
    let html;

    client.getEntry('4MykPA3pSoQaUGwGW0q82e')
      .then((entry) => {
        html = documentToHtmlString(entry.fields.accessoriVino);
        this.setState({
          data: html,
        });
      });
  }
  openLightbox(event, obj) {
    this.setState({
      currentImage: obj.index,
      lightboxIsOpen: true,
    });
  }
  closeLightbox() {
    this.setState({
      currentImage: 0,
      lightboxIsOpen: false,
    });
  }
  gotoPrevious() {
    this.setState({
      currentImage: this.state.currentImage - 1,
    });
  }
  gotoNext() {
    this.setState({
      currentImage: this.state.currentImage + 1,
    });
  }
  render() {
    const html = this.state.data;
    return (
      <Layout>
        <Navbar />
        <div className="wrapper">
            <div className={`container ${style.height100}`}>
              {/* <h1><span className={`${style.highlight}`}>accessoriVino</span></h1> */}
              <div className="row">
                <div className="col">
                  <div className={`${style.content} container`} dangerouslySetInnerHTML={{ __html: html }} ></div>
                </div>
                <div className={`container ${style.gallery}`}>
                  <Gallery photos={photos} onClick={this.openLightbox} margin={12}/>
                  <Lightbox images={photos}
                    onClose={this.closeLightbox}
                    onClickPrev={this.gotoPrevious}
                    onClickNext={this.gotoNext}
                    currentImage={this.state.currentImage}
                    isOpen={this.state.lightboxIsOpen}
                  />
                </div>
              </div>
            </div>
        </div>
        <Footer />
      </Layout>
    );
  }
};

export default accessoriVino;
